<script>
  function isTencentWx() {
    var ua = navigator.userAgent.toLowerCase();
    return ua.match(/MicroMessenger/i) == "micromessenger";
  }

  let slogan = "A weekly newsletter of AR Development, AR apps and games.";
  let subscribed = new URLSearchParams(location.search).get("subscribed");

  let message = {
    pending: "Please check your email to confirm your subscription.",
    confirmed: "Thank you. Your subscription is now confirmed."
  }[subscribed];

  if (subscribed) {
    history.replaceState({}, "", "/");
  }

  let curatedAPI = isTencentWx()
    ? "http://curator.argoodies.com/email_subscribers"
    : "https://argoodies.curated.co/email_subscribers";
</script>

<style lang="scss">
  @import "./common/style/constant.scss";
  .logo-root {
    margin: 80px 0 0;
    text-align: center;
    .logo {
      margin: 0 auto;
    }
    img {
      border: none;
      height: 120px;
    }
    @media (min-width: 640px) {
      img {
        height: 20;
      }
    }
  }

  .slogan-wrap {
    margin: 40px 0 80px;
    text-align: center;
    .slogan {
      color: $White;
      margin: 0 auto;
      font-size: 20px;
      max-width: 640px;
      line-height: 1.3;
      font-weight: 800;
      text-align: center;
    }
  }

  .message-wrap {
    color: $White;
    margin: 0 auto;
    font-size: 14px;
    max-width: 600px;
    font-weight: bold;
    padding: 10px 20px;
    text-align: center;
    border-top: solid 2px #fff;
    background-color: rgba(255, 255, 255, 0.3);
  }

  .tip-wrap {
    .tip {
      text-align: center;
      font-weight: 600;
      font-size: 12px;
      margin: 0 auto;
      color: $White;
    }
  }

  .subscribe-form-root {
    margin: 20px 0 20px;
    text-align: center;
    .subscribe-form {
      box-shadow: inset 0 0 12px $White;
      max-width: 600px;
      margin: 0 auto;
      .left-radius {
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 0;
        border-top-left-radius: 4px;
        border-top-right-radius: 0;
      }
      .right-radius {
        border-bottom-right-radius: 4px;
        border-bottom-left-radius: 0;
        border-top-right-radius: 4px;
        border-top-left-radius: 0;
      }
      .email-input {
        background-color: transparent;
        border: 1px solid $White;
        caret-color: $White;
        padding: 12px 16px;
        font-weight: bold;
        line-height: 1.4;
        font-size: 14px;
        outline: none;
        color: $White;
        height: 52px;
        float: left;
        width: 70%;
        margin: 0;
        &::placeholder {
          color: $White;
          opacity: 0.8;
        }
        &:focus {
          padding-left: 15px;
          border-width: 2px;
        }
      }
      .submit-button {
        border-left: none;
        border-top: 1px solid $White;
        border-right: 1px solid $White;
        border-bottom: 1px solid $White;
      }
      .submit-button {
        box-shadow: inset 0 0 18px $White;
        background-color: transparent;
        text-shadow: 0 0 6px $White;
        text-align: center;
        font-weight: bold;
        cursor: pointer;
        font-size: 14px;
        color: $White;
        height: 52px;
        padding: 0;
        width: 30%;
        margin: 0;
        &:hover {
          box-shadow: inset 0 0 24px $White;
        }
      }
    }
  }
</style>

<div class="logo-root">
  <a class="logo" href="/">
    <img alt="AR Goodies" src="./asset/image/logo.png" />
  </a>
</div>
<div class="slogan-wrap">
  <p class="slogan">{slogan}</p>
</div>
{#if subscribed}
  <div class="message-wrap">{message}</div>
{:else}
  <div class="subscribe-form-root">
    <form
      action={curatedAPI}
      id="embedded-subscribe-form"
      class="subscribe-form"
      method="post">
      <input
        id="subscribe-email"
        placeholder="Your email address"
        class="left-radius email-input"
        name="email"
        type="email" />
      <input name="form_dom_id" value="embedded-subscribe-form" type="hidden" />
      <button
        type="submit"
        class="right-radius submit-button"
        onclick="this.form.submit(); this.disabled=true; this.value='SENDING';"
        value="SUBSCRIBE">
        SUBSCRIBE
      </button>
    </form>
  </div>
  <div class="tip-wrap">
    <p class="tip">
      Subscribe with email. We'll never share your email address and you can opt
      out at any time.
    </p>
  </div>
{/if}
